<template>
  <div class="container">
    <div class="card">
      <div class="card-action">
        <h4 class="center orange-text text-darken-3"  >Aslanyan`s Chat v1.1</h4>
        <hr>
        <div class="card-content" ref="content">
          <ul class="messages">
            <li v-for="chat of chats" 
                :class="name == chat.name ? 'current' : 'another' ">
              <span class="name red-text text-darken-2">{{ name == chat.name ? 'You' :  chat.name}}</span>
              <p class="message aqua-text">{{chat.text}}</p>
              <span class="time cyan-text lighten-4">{{chat.createdAt.time}}</span>
              <span class="date cyan-text lighten-4">{{chat.createdAt.date}}</span>
         
            </li>
          </ul>
        </div>
        <new-message :name="name" :content="this.$refs['content']"/>
      </div>
    </div>
  </div>
</template>

<script>
import NewMessage from '@/components/NewMessage';
import db from '../firebase/init.js';


export default {
    components:{
      NewMessage,
    },
    data(){
      return{
        name:document.cookie.split('=')[1]
      }
    },
    firestore() {
      return {
        chats: db.collection('chats').orderBy('createdAt')
                
      }
    },


}
</script>

<style lang="scss" scoped>
.card-content{
  max-height: 60vh;
  overflow-y: scroll;
  position: relative;
  z-index: 1;
}
.name{
    text-transform: capitalize;
    margin-right: 0.5rem;
  }
.message{
  word-break: break-all;
  font-weight: 600;
  padding: .5rem;
}
.time , .date{display: block; text-align: right;}
.date{ font-size: .75rem;}
.messages{ 
  margin-top: .5rem;
  li{
    margin-top: 1rem;
    padding: 0.5rem;
    border-radius: 10px;
    position: relative;
    &:after{
      content: '';
      position: absolute;
      top: calc(50% - 8px);
      transform: rotate(45deg);
      z-index: -1;
      height: 16px;
      width: 16px;
      background: inherit;
    }
  }
  .current , .another{
    clear:both;
  }
  .current{
    background: #ede7f6;
    float: right;
    &:after{ right: -8px; }
  }
  .another{
    background:#bbdefb;
    float: left;
    &:after{left:-8px;}

  }
}
</style>




